import { Component, OnInit } from '@angular/core';
// import { MessageTempTotal1, mesgSendChanellSequ1, HourTime, MinTime } from './../../../constant/codeValue';
import * as _ from 'lodash';
import { Message, ConfirmationService } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { updateInfoConfineSendRuleBean } from './../bean/message-rule.bean';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';

@Component({
    selector: 'update-message-rule',
    templateUrl: './update-message-rule.component.html',
    styleUrls: ['./update-message-rule.component.scss'],
    providers: [ConfirmationService, Commfunc],
})
export class UpdateMessageRuleComponent implements OnInit {

    // 
    data: any = '';
    // 
    messageTempTotal: any = [];
    mesgSendChanellSequ: any = [];
    hourTime: any = [];
    minTime: any = [];


    // 
    msgs: Message[] = [];
    // 

    channelType: any[];//
    // 
    messageSendTimeLimit1Hour: any = '0';//
    messageSendTimeLimit1Min: any = '0';//
    // 
    messageSendTimeLimit2Hour: any = '0';//
    messageSendTimeLimit2Min: any = '0';//
    messageSendTimeLimit1Sec: string = '00';
    messageSendTimeLimit2Sec: string = '00';
    // 
    // bean
    updateInfoConfineSendRuleBean: updateInfoConfineSendRuleBean = new updateInfoConfineSendRuleBean();//
    //
    code: any;
    userform: FormGroup; //

    constructor(
        public httpService: CustomeHttpService,
        public routerInfo: ActivatedRoute,
        public router: Router,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.codeValues() //
        //,
        this.channelType = this.code['mesgSendChanellSequ1'];
        this.hourTime = this.code['HourTime'];
        this.minTime = this.code['MinTime'];
    }
    ngOnInit() {
        this.userform = this.fb.group({
            'templateId': new FormControl('', [Validators.required]),
            'templateName': new FormControl('', [Validators.required]),
            'channelType': new FormControl('', [Validators.required]),
        });
        let data = JSON.parse(this.routerInfo.snapshot.params.data)
        this.updateInfoConfineSendRuleBean.templateId = data.templateId;
        this.updateInfoConfineSendRuleBean.templateName = data.templateName;
        this.updateInfoConfineSendRuleBean.channelType = data.channelType;
        this.updateInfoConfineSendRuleBean.limitTimeStart = data.limitTimeStart;
        this.updateInfoConfineSendRuleBean.limitTimeEnd = data.limitTimeEnd;
        this.timeSplit();

    }

    timeSplit() {
        //
        let limitTimeStart = this.updateInfoConfineSendRuleBean.limitTimeStart.split(':');
        let limitTimeEnd = this.updateInfoConfineSendRuleBean.limitTimeEnd.split(':');
        this.messageSendTimeLimit1Hour = limitTimeStart[0];
        this.messageSendTimeLimit1Min = limitTimeStart[1];
        this.messageSendTimeLimit1Sec = limitTimeStart[2];
        this.messageSendTimeLimit2Hour = limitTimeEnd[0];
        this.messageSendTimeLimit2Min = limitTimeEnd[1];
        this.messageSendTimeLimit2Sec = limitTimeEnd[2];
    }
    submit() {

        //
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        //
        this.updateInfoConfineSendRuleBean.limitTimeStart = `${this.messageSendTimeLimit1Hour}:${this.messageSendTimeLimit1Min}:${this.messageSendTimeLimit1Sec}`;
        this.updateInfoConfineSendRuleBean.limitTimeEnd = `${this.messageSendTimeLimit2Hour}:${this.messageSendTimeLimit2Min}:${this.messageSendTimeLimit2Sec}`;
        // 
        if (this.updateInfoConfineSendRuleBean.limitTimeStart == this.updateInfoConfineSendRuleBean.limitTimeEnd) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return;
        }
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                this.updateInfoConfineSendRule();
            }
        });
    }

    updateInfoConfineSendRule() {
        // 
        this.httpService.updateMsgTimeLimit(this.updateInfoConfineSendRuleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: '' }];
                this.router.navigate(['/pages/tzb/uisftech-view/message-rule-management']);
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }

    windowReSet() {
        // 
        this.messageSendTimeLimit1Hour = '00';//
        this.messageSendTimeLimit1Min = '00';//
        this.messageSendTimeLimit2Hour = '00';//
        this.messageSendTimeLimit2Min = '00';//
        this.messageSendTimeLimit1Sec = '00';
        this.messageSendTimeLimit2Sec = '00';
        // this.updateInfoConfineSendRuleBean = new updateInfoConfineSendRuleBean();//
    }

    return() {
        // 
        this.router.navigate(['/pages/tzb/uisftech-view/message-rule-management']);
    }

    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
}